@import "website/variables";
@import "admin/global";
@import "admin/jquery-ui-overrides";
@import "admin/data-table-overrides";
@import "website/mixins";
//@import "admin/modal";
@import "admin/header";
@import "admin/main";
@import "admin/sidebar";
@import "admin/footer";

#user-profile-page,
#messages-page,
#users-page,
#recipient-page,
#dashboard-page {
  label.error {
    position: absolute;
    top: 100%;
    color: #bd2440;

  }
}

.modal {
  z-index: 9999;
}

.img-fake img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid rgba(7, 7, 7, .1);
  margin-top: 30px;

  label {
    top: 10px !important;
  }
}

.main {
  .card {
    .card-head {
      background-image: none;
      background-color: rgba(255, 255, 255, 1);
      .fa {
        color: #bd2440;
      }
    }
    .card-body {
      //            padding: 5px;
    }
  }

  .overview-cards .card:hover {
    border: 1px solid #bd2440;
    .card-head {
      background: #bd2440;
      border: 1px solid #bd2440;
      .fa {
        color: #fff;
      }
    }
  }
}

#task-modal {
  .modal-header {
    padding: 5px 10px;
  }
  .modal-footer {
    padding: 5px 10px;
  }
}

.card-body {
  ul {
    li {
      display: flex;
      justify-content: space-between;

      span:first-of-type {
        font-weight: bold;
      }
    }
  }
}

table.dataTable th {
  padding-left: 25px !important;
}

table.dataTable thead .sorting, table.dataTable thead .sorting_asc, table.dataTable thead .sorting_desc {
  background-position: center left;

}

.form-control:disabled, .form-control[readonly] {
  background: #fff !important;
}

.c-dropdown label:after {
  content: '\f107';
  font-family: FontAwesome;
  position: absolute;
  top: 0;
  right: 5px;
  z-index: 999999;
}


